"use client";
import React, {
  useState,
  createContext,
  useEffect,
  useMemo,
  useContext,
} from "react";
import api from "@/app/api/Api";
import { handleAsync } from "@/utils/error.helper";
import { ISubscripcion } from "@/interfaces/Subscripcion.interface";
import UserContext from "@/context/user.context";

interface SubscriptionContextProps {
  sub: ISubscripcion | null;
  suscribirse: (
    planId: number,
    paymentMethodToken: string,
    email: string
  ) => Promise<void>;
  desuscribirse: () => Promise<void>;
  fetchSub: () => Promise<ISubscripcion | null>;
  fetchPlan: (subscription?: ISubscripcion) => Promise<any>;
}

const defaultSubscriptionContext: SubscriptionContextProps = {
  sub: null,
  suscribirse: async () => {},
  desuscribirse: async () => {},
  fetchSub: async () => null,
  fetchPlan: async () => null,
};

export const SubscriptionContext = createContext<SubscriptionContextProps>(
  defaultSubscriptionContext
);

export const SubscriptionProvider = ({
  children,
}: {
  children: React.ReactNode;
}) => {
  const { user } = useContext(UserContext);
  const [sub, setSub] = useState<ISubscripcion | null>(null);

  const suscribirse = async (
    planId: number,
    paymentMethodToken: string,
    email: string
  ) => {
    try {
      const { data, error } = await handleAsync(
        api.post(`/subscriptions`, {
          planId,
          userEmail: email,
          paymentMethodToken,
        })
      );
      if (error || !data?.data?.subscription) {
        console.error(
          "Error al suscribirse:",
          error || "No se retornaron datos"
        );
        return;
      }
      const newSubscription: ISubscripcion = data.data.subscription;
      setSub(newSubscription);
      localStorage.setItem("subscripcion", JSON.stringify(newSubscription));
    } catch (err) {
      console.error("Excepción en suscribirse:", err);
    }
  };

  const desuscribirse = async () => {
    if (!sub) return;
    try {
      const { data, error } = await handleAsync(
        api.post(`/subscriptions/cancel`, {
          subscriptionId: sub.mercadopagoSubscriptionId,
          cancellationReason: "Cancelación solicitada por el usuario",
        })
      );
      if (error || !data?.data?.subscription) {
        console.error(
          "Error al desuscribirse:",
          error || "No se retornaron datos"
        );
        return;
      }
      setSub(null);
      localStorage.removeItem("subscripcion");
    } catch (err) {
      console.error("Excepción en desuscribirse:", err);
    }
  };

  const fetchSub = async (): Promise<ISubscripcion | null> => {
    if (!user) return null;
    const { data, error } = await handleAsync(api.get(`/users/sub/${user.id}`));
    if (error || !data?.data) {
      console.error(
        "Error al obtener suscripción:",
        error || "No se retornaron datos"
      );
      return null;
    }
    const newSub: ISubscripcion = data.data;
    setSub(newSub);
    localStorage.setItem("subscripcion", JSON.stringify(newSub));
    // Se puede llamar a fetchPlan(newSub) aquí si se requiere actualizar el plan asociado
    return newSub;
  };

  const fetchPlan = async (subscription?: ISubscripcion) => {
    const currentSub = subscription || sub;
    if (!currentSub || !currentSub.plan) {
      console.error("No hay suscripción activa para obtener el plan.");
      return null;
    }
    const { data, error } = await handleAsync(
      api.get(`/users/plan/${currentSub.plan.id}`)
    );
    console.log("Respuesta de la API del plan:", data);
    if (error || !data?.data) {
      console.error(
        "Error al obtener el plan:",
        error || "No se retornaron datos"
      );
      return null;
    }
    return data.data;
  };

  // Cargar suscripción almacenada o actualizarla cuando el usuario cambie
  useEffect(() => {
    if (!user) return;
    const storedSub = localStorage.getItem("subscripcion");
    if (storedSub) {
      setSub(JSON.parse(storedSub));
    }
    fetchSub();
  }, [user]);

  const value = useMemo(
    () => ({
      sub,
      suscribirse,
      desuscribirse,
      fetchSub,
      fetchPlan,
    }),
    [sub, user]
  );

  return (
    <SubscriptionContext.Provider value={value}>
      {children}
    </SubscriptionContext.Provider>
  );
};
